  .hamburgerMenu {
    display: none;
  }
  .menu {
    display: flex;
    flex-direction: row;
    transform: translateX(0);
    position: relative;
    padding: 0px;
  }
  .menu a {
    padding-inline: 22px;
  }
  .menu a span {
    font-size: 12px;
  }
  .cardContent {
    grid-template-columns: 300px 300px;
    gap: 30px;
  }
  .details-content {
    padding: 20px;
  }
  .details-content img {
    max-height: 300px;
  }
  .descDetailsContent {
    font-size: 16px;
  }
  .variantMenuMakanan,
  .variantMenuMinuman {
    width: 45%;
  }
  .popupContent {
    width: 80%;
  }
  footer span {
    font-size: 12px;
  }
}

@media only screen and (min-width: 1024px) and (max-width: 1439px) {
  body {
    height: 100vh;
  }
  .hamburgerMenu {
    display: none;
  }
