    display: flex;
    flex-direction: row;
    transform: translateX(0);
    position: relative;
    padding: 0px;
  }
  .menu a {
    padding-inline: 22px;
  }
  .menu a span {
    font-size: 12px;
  }
  .cardContent {
    grid-template-columns: 300px 300px 300px;
    gap: 20px 30px;
  }
  .details-content {
    padding: 30px;
  }
  .details-content img {
    max-height: 400px;
  }
  .descDetailsContent p {
    font-size: 18px;
  }
  .variantMenuMakanan,
  .variantMenuMinuman {
    width: 200px;
